'use client';

import { useState, useEffect } from 'react';
import Link from 'next/link';
import { usePathname } from 'next/navigation';
import { ChevronDown, ChevronRight } from 'lucide-react';

interface NavChild {
  name: string;
  href: string;
  icon: string;
  isBrand?: boolean;
}

interface NavItem {
  name: string;
  href?: string;
  icon: string;
  isBrand?: boolean;
  id?: string;
  children?: NavChild[];
}

interface NavGroup {
  label?: string;
  items: NavItem[];
}

// Navigation structure with grouped sections
const navigationGroups: NavGroup[] = [
  {
    items: [
      { name: 'Dashboard', href: '/', icon: 'fa-chart-line' },
      { name: 'Inventory', href: '/inventory', icon: 'fa-boxes-stacked' },
      { name: 'Orders', href: '/orders', icon: 'fa-shopping-cart' },
      { name: 'Returns', href: '/returns', icon: 'fa-undo-alt' },
    ]
  },
  {
    label: 'Fulfillment',
    items: [
      {
        name: 'Pick & Pack',
        id: 'pick-pack',
        icon: 'fa-dolly',
        children: [
          { name: 'Overview', href: '/pick-pack', icon: 'fa-clipboard-list' },
          { name: 'Pick Station', href: '/pick-pack/pick', icon: 'fa-hand-pointer' },
          { name: 'Pack Station', href: '/pick-pack/pack', icon: 'fa-box' },
          { name: 'FBA Prep', href: '/pick-pack/fba', icon: 'fa-amazon', isBrand: true },
        ]
      },
      { name: 'Shipping', href: '/shipping', icon: 'fa-truck-fast' },
    ]
  },
  {
    label: 'Operations',
    items: [
      { name: 'Purchase Orders', href: '/purchase-orders', icon: 'fa-file-invoice' },
      { name: 'Suppliers', href: '/suppliers', icon: 'fa-truck-loading' },
      { name: 'Work Orders', href: '/work-orders', icon: 'fa-industry' },
      { name: 'Bundles', href: '/bundles', icon: 'fa-layer-group' },
      { name: 'Cycle Counts', href: '/inventory/counts', icon: 'fa-clipboard-check' },
      { name: 'Transfers', href: '/inventory/transfers', icon: 'fa-exchange-alt' },
      { name: 'Reports', href: '/reports', icon: 'fa-chart-pie' },
      { name: 'Settings', href: '/settings', icon: 'fa-cog' },
    ]
  }
];

// Sync status data
const syncStatus = [
  { name: 'Veeqo', status: 'Synced' },
  { name: 'Shopify', status: 'Synced' },
];

export default function Sidebar() {
  const pathname = usePathname();
  const [expandedMenus, setExpandedMenus] = useState<string[]>([]);

  // Auto-expand menu based on current route
  useEffect(() => {
    if (pathname.startsWith('/pick-pack')) {
      setExpandedMenus(prev =>
        prev.includes('pick-pack') ? prev : [...prev, 'pick-pack']
      );
    }
  }, [pathname]);

  const toggleMenu = (menuId: string) => {
    setExpandedMenus(prev =>
      prev.includes(menuId)
        ? prev.filter(m => m !== menuId)
        : [...prev, menuId]
    );
  };

  const isActive = (href: string) => {
    if (href === '/') {
      return pathname === '/';
    }
    return pathname === href || pathname.startsWith(href + '/');
  };

  const isMenuActive = (item: NavItem) => {
    if (item.children) {
      return item.children.some(child => isActive(child.href));
    }
    return item.href ? isActive(item.href) : false;
  };

  return (
    <aside className="fixed left-0 top-0 z-40 h-screen w-64 bg-slate-900/80 border-r border-slate-700/50 flex flex-col">
      {/* Logo */}
      <div className="p-5 border-b border-slate-700/50">
        <div className="flex items-center gap-3">
          <div className="w-10 h-10 bg-gradient-to-br from-emerald-400 to-emerald-600 rounded-xl flex items-center justify-center">
            <i className="fas fa-leaf text-white text-lg"></i>
          </div>
          <div>
            <h1 className="font-bold text-white text-lg">Cronk WMS</h1>
            <p className="text-xs text-slate-400">Inventory Management</p>
          </div>
        </div>
      </div>

      {/* Navigation */}
      <nav className="flex-1 p-4 space-y-1 overflow-y-auto">
        {navigationGroups.map((group, groupIndex) => (
          <div key={groupIndex}>
            {/* Section Header */}
            {group.label && (
              <div className="pt-4 pb-2">
                <p className="px-4 text-xs font-semibold text-slate-500 uppercase tracking-wider">
                  {group.label}
                </p>
              </div>
            )}

            {/* Navigation Items */}
            {group.items.map((item) => {
              // Expandable menu with children
              if (item.children && item.id) {
                const isExpanded = expandedMenus.includes(item.id);
                const menuIsActive = isMenuActive(item);

                return (
                  <div key={item.id}>
                    {/* Parent button */}
                    <button
                      onClick={() => toggleMenu(item.id!)}
                      className={`w-full flex items-center gap-3 px-4 py-3 rounded-lg transition-all ${
                        menuIsActive
                          ? 'bg-gradient-to-r from-emerald-500/20 to-transparent border-l-2 border-emerald-400 text-white'
                          : 'text-slate-400 hover:text-white hover:bg-slate-800/50'
                      }`}
                    >
                      <i className={`fas ${item.icon} w-5 ${menuIsActive ? 'text-emerald-400' : ''}`}></i>
                      <span className={menuIsActive ? 'font-medium' : ''}>{item.name}</span>
                      <span className="ml-auto">
                        {isExpanded ? (
                          <ChevronDown className="w-4 h-4 text-slate-500" />
                        ) : (
                          <ChevronRight className="w-4 h-4 text-slate-500" />
                        )}
                      </span>
                    </button>

                    {/* Children */}
                    {isExpanded && (
                      <div className="ml-4 mt-1 space-y-1 border-l border-slate-700/50 pl-3">
                        {item.children.map((child) => {
                          const childActive = isActive(child.href);
                          return (
                            <Link
                              key={child.href}
                              href={child.href}
                              className={`flex items-center gap-3 px-4 py-2 rounded-lg transition-all text-sm ${
                                childActive
                                  ? 'bg-emerald-500/10 text-emerald-400'
                                  : 'text-slate-400 hover:text-white hover:bg-slate-800/50'
                              }`}
                            >
                              <i className={`${child.isBrand ? 'fab' : 'fas'} ${child.icon} w-4 text-xs ${childActive ? 'text-emerald-400' : ''}`}></i>
                              <span>{child.name}</span>
                            </Link>
                          );
                        })}
                      </div>
                    )}
                  </div>
                );
              }

              // Regular menu item
              const active = item.href ? isActive(item.href) : false;
              return (
                <Link
                  key={item.name}
                  href={item.href || '#'}
                  className={`flex items-center gap-3 px-4 py-3 rounded-lg transition-all ${
                    active
                      ? 'text-white bg-gradient-to-r from-emerald-500/20 to-transparent border-l-2 border-emerald-400'
                      : 'text-slate-400 hover:text-white hover:bg-slate-800/50'
                  }`}
                >
                  <i className={`${item.isBrand ? 'fab' : 'fas'} ${item.icon} w-5 ${active ? 'text-emerald-400' : ''}`}></i>
                  <span className={active ? 'font-medium' : ''}>{item.name}</span>
                </Link>
              );
            })}
          </div>
        ))}
      </nav>

      {/* Sync Status */}
      <div className="p-4 border-t border-slate-700/50">
        <div className="bg-slate-800/50 backdrop-blur border border-slate-700/50 rounded-lg p-3 space-y-2">
          {syncStatus.map((sync) => (
            <div key={sync.name} className="flex items-center justify-between text-xs">
              <span className="text-slate-400">{sync.name}</span>
              <span className="flex items-center gap-1 text-emerald-400">
                <span className="w-1.5 h-1.5 bg-emerald-400 rounded-full animate-pulse"></span>
                {sync.status}
              </span>
            </div>
          ))}
        </div>
      </div>
    </aside>
  );
}
